<script>
export default {
  name: "App2",

  components: {},

  data: () => ({
    items: {
      colours: [
        { color: "Black", id: 1 },
        { color: "Red", id: 2 },
        { color: "White", id: 3 },
        { color: "Gold", id: 4 },
      ],
      isColor: {
        color: "",
      },
      //   selectedOptionId: 0,

      matte: false,
      surface: ["Brick", "Stucco", "Stone", "Other"],
    },

    isSurface: "",

    overlay: "",

    cost: 0,

    radio: {
      value: "",
    },
  }),

  computed: {
    colorIs: function() {
      return this.isColor.color;
    },

    calcuateCost: function() {
      if (this.items.matte == true) {
        return this.cost + this.overlay.length * 6 + 10;
      } else {
        return this.cost + this.overlay.length * 6;
      }
    },
    font: function() {
      if (this.radio.value == 1) {
        return "Edward";
      } else if (this.radio.value == 2) {
        return "Script";
      } else if (this.radio.value == 3) {
        return "Gessele";
      } else if (this.radio.value == 4) {
        return "Slimlines";
      } else if (this.radio.value == 5) {
        return "Copasetic";
      } else if (this.radio.value == 6) {
        return "Aerolite";
      } else {
        return "";
      }
    },
    // methods: {

    //   },
  },
};
</script>

